'use strict';

var React = require('react/addons');
var FilterStore = require('../stores/filter');
var LayoutStore = require('../stores/layout');
var filterActions = require('../actions/filter');
var Navigation = require('react-router').Navigation;
var Button = React.createFactory(require('./modules/button.jsx'));

function getFilterState() {
    return {
        isOpen: FilterStore.getStatus()
    }
}

var filterComponent = React.createClass({displayName: "filterComponent",
    mixins: [FilterStore.mixin, Navigation],
    statics: {
        willTransitionFrom: function(transition, component) {
            LayoutStore.setCustomAnimation('filter'); 
        }
    },
    getInitialState: function() {
        return getFilterState();
    },

    render: function() {
        var cx = React.addons.classSet;
        var classString = cx({
            'filter': true,
            'open': this.state.isOpen
        });

        return (
            /* jshint ignore:start */
            React.createElement("div", {className: classString}, 
                React.createElement("h3", null, "Filter properties ", React.createElement(Button, {icon: "x", className: "right", onClick: this.close})), 
                React.createElement("div", {className: "form"}, 
                    React.createElement("div", {className: "row"}, 
                        React.createElement("input", {className: "city", type: "text", placeholder: "Location (city, region..)"})
                    ), 
                    React.createElement("div", {className: "row"}, 
                        React.createElement("input", {type: "text", className: "rooms half-width", placeholder: "Rooms"}), 
                        React.createElement("input", {type: "text", className: "surface half-width", placeholder: "Surface"})
                    ), 
                    React.createElement("div", {className: "row"}, 
                        React.createElement("div", {className: "slider"}, 
                            React.createElement("div", {className: "bar"}, 
                                React.createElement(Button, {icon: "indent-more", className: "more", onClick: this.budget}), 
                                React.createElement(Button, {icon: "indent-less", className: "less", onClick: this.budget})
                            ), 
                            React.createElement("span", {className: "min"}, "0€"), 
                            React.createElement("span", {className: "max"}, "900.000€+")
                        )
                    ), 
                    React.createElement("div", {className: "row"}, 
                        React.createElement("div", {className: "type"}, 
                            React.createElement("img", {src: "images/house.png"}), 
                            React.createElement("span", null, "Maison")
                        ), 
                        React.createElement("div", {className: "type"}, 
                            React.createElement("img", {src: "images/private.png"}), 
                            React.createElement("span", null, "Appartment")
                        ), 
                        React.createElement("div", {className: "type last"}, 
                            React.createElement("img", {src: "images/furniture.png"}), 
                            React.createElement("span", null, "Something")
                        )
                    ), 
                    React.createElement(Button, {text: "Filter", className: "submit", onClick: this.goToList})
                )
            )
            /* jshint ignore:end */
        );
    },

    _onChange: function() {
        this.setState(getFilterState());
    },

    close: function() {
        history.back();
    },

    budget: function() {

    },

    goToList: function() {
        this.transitionTo('list');
    }

});

module.exports = filterComponent;
