'use strict';

var React = require('react/addons');
var pageStore = require('../../stores/page');


var SideBarComponent = React.createClass({displayName: "SideBarComponent",
    mixins: [pageStore.mixin],
    render: function() {
        var cx = React.addons.classSet;
        var classString = cx({
            'sidebar': true
        });

        return (
            /* jshint ignore:start */
            React.createElement("ul", {className: classString}, 
                React.createElement("li", null, React.createElement("a", {href: "/"}, "Home")), 
                React.createElement("li", null, React.createElement("a", {href: "#"}, "Link 1")), 
                React.createElement("li", null, React.createElement("a", {href: "#"}, "Link 2")), 
                React.createElement("li", null, React.createElement("a", {href: "#"}, "Link 3"))
            )
            /* jshint ignore:end */
        );
    }

});

module.exports = SideBarComponent;
