'use strict';

var React = require('react');
var If = require('../helpers/if.jsx');

var ButtonComponent = React.createClass({displayName: "ButtonComponent",

    render: function() {
        return  React.createElement("button", {onClick: this.props.onClick, className: this.props.className}, 
                    React.createElement("i", {className: "fi-" + this.props.icon}), 
                    React.createElement(If, {test: this.props.text}, 
		                React.createElement("span", null, this.props.text)
		            )
                )
    }

});

module.exports = ButtonComponent;
