  'use strict';

var React = require('react/addons');
var pageStore = require('../../stores/page');
var SideBarComponent = React.createFactory(require('../modules/sidebar.jsx'));
var TopBarComponent = React.createFactory(require('../modules/topbar.jsx'));
var Router = require('react-router');
var RouteHandler = require('react-router').RouteHandler;
var SnapContent = require('../../libs/snap/SnapContent.jsx');
var SnapDrawerLeft = require('../../libs/snap/SnapDrawerLeft.jsx');

var getState = function() {
    return {
        title: pageStore.getMetaData().title,
        isSidebarOpen: pageStore.getSidebarStatus()
    };
};


var DefaultLayout = React.createClass({displayName: "DefaultLayout",
    mixins: [pageStore.mixin, Router.State],
    componentDidMount: function() {
        pageStore.emitChange();
    },
    getInitialState: function() {
        return getState();
    },
    render: function() {
        var cx = React.addons.classSet,
            mainClasses = cx({
                'main-container': true,
                'left': this.state.isSidebarOpen
            });

        return (
        /* jshint ignore:start */
        React.createElement("div", {className: "default layout"}, 
            React.createElement("div", {className: "snap-drawers"}, 
                React.createElement(SnapDrawerLeft, null, 
                    React.createElement(SideBarComponent, null)
                )
            ), 
            React.createElement(SnapContent, null, 
                React.createElement(TopBarComponent, null), 
                this.props.children
            )
        )
        /* jshint ignore:end */
        );
    },
    // Event handler for 'change' events coming from store mixins.
    _onChange: function() {
        this.setState(getState());
    }
});

module.exports = DefaultLayout;
