var React = require('react');
var LayoutStore = require('../stores/layout');
var Router = require('react-router');
var ReactCSSTransitionGroup = require('react/lib/ReactCSSTransitionGroup');
var RouteHandler = require('react-router').RouteHandler;

var App = React.createClass({displayName: "App",

    mixins: [ LayoutStore.mixin, Router.State ],
    getStoreData: function() {
        var name = this.getRoutes().reverse()[0].name;

        return {
            currentLayout: LayoutStore.getCurrentLayout(name),
            currentPage: name
        }
    },
    getInitialState: function() {
        return this.getStoreData();
    },
    componentWillReceiveProps: function(){
        this.setState(this.getStoreData()) // creates a double refresh for some reason
    },
    render: function() {
        return (
            React.createElement(ReactCSSTransitionGroup, {component: "div", transitionName: this.state.currentLayout.transitionName}, 
                React.createElement(this.state.currentLayout.component, {className: "layout", key: this.state.currentLayout.name, customAnimation: this.state.customAnimation}, 
                    React.createElement(ReactCSSTransitionGroup, {component: "div", transitionName: this.state.currentLayout.transitionName}, 
                        React.createElement(RouteHandler, {className: "page", key: this.state.currentPage})
                    )
                )
            )
        );
    },

    _onChange: function() {
        this.setState(this.getStoreData())
    }
});

module.exports = App;