var React = require('react');
var Navigation = require('react-router').Navigation;

var ListItem = React.createClass({displayName: "ListItem",
    mixins: [Navigation],

    propTypes: {
        item: React.PropTypes.object.isRequired
    },

    render: function() {
        var item = this.props.item;

        return (
            React.createElement("li", {className: "item", onClick: this.goToItem, id: item.id}, 
                React.createElement("div", {className: "image"}, 
                    React.createElement("span", {className: "price"}, item.price, "€"), 
                    React.createElement("span", {className: "agent"}, item.agent)
                ), 
                React.createElement("h2", null, item.title), 
                React.createElement("h3", null, item.description)
            )
        );
    },

    goToItem: function(e) {
        this.transitionTo('property', {propertyId: e.currentTarget.id});
    }

});

module.exports = ListItem;