'use strict';

var React = require('react');
var Navigation = require('react-router').Navigation;
var Button = React.createFactory(require('../modules/button.jsx'));

var StaticLayout = React.createClass({displayName: "StaticLayout",
    mixins: [Navigation],

    render: function() {
        return (
            /* jshint ignore:start */
                React.createElement("div", {className: "layout static"}, 
                    React.createElement("div", {className: "main-container"}, 
                        React.createElement("div", {className: "topbar"}, 
                            React.createElement(Button, {icon: "arrow-left", onClick: this.back, className: "back-btn"})
                        ), 
                        React.createElement("div", {className: "content"}, 
                            this.props.children
                        )
                    )
                )
            /* jshint ignore:end */
        );
    },

    back: function(){
        history.back()
    }
});

module.exports = StaticLayout;