var React = require('react');
var cloneWithProps = require('react/lib/cloneWithProps');

var Tabs = React.createClass({displayName: "Tabs",
  getInitialState: function () {
    return {
      selectedTab: 1,
      content: null
    };
  },
  selectTab: function (options) {
    this.setState(options);
  },
  render: function () {
    var children = React.Children.map(this.props.children, function (child, index) {
      return cloneWithProps(child, {
        active: (index === this.state.selectedTab),
        index: index,
        selectTab: this.selectTab
      });
    }.bind(this));
    return (
      React.createElement("div", {className: "row tabWrapper"}, 
        React.createElement("ul", {className: "tabs"}, children), 
        React.createElement("div", {className: "tabs-content"}, this.state.content)
      )
    );
  }
});

module.exports = Tabs;
Tabs.Tab = require('./tab');